import heroModel from "@/assets/hero-model.jpg";

const HeroSection = () => {
  return (
    <section id="home" className="relative min-h-screen flex items-center">
      {/* Background Image */}
      <div className="absolute inset-0">
        <img
          src={heroModel}
          alt="Wasfah Premium Formal Attire"
          className="w-full h-full object-cover object-center"
        />
        <div className="absolute inset-0 bg-gradient-to-r from-background via-background/80 to-transparent" />
        <div className="absolute inset-0 bg-gradient-to-t from-background via-transparent to-transparent" />
      </div>

      {/* Content */}
      <div className="relative container mx-auto px-6 mt-14 pt-24">
        <div className="max-w-2xl">
          {/* Tagline */}
          <p className="font-body text-primary text-sm tracking-[0.3em] uppercase mb-6 opacity-0 animate-fade-up">
            Premium Formal Attire
          </p>

          {/* Main Heading */}
          <h1 className="font-display text-5xl md:text-7xl lg:text-8xl font-light text-foreground leading-tight mb-8 opacity-0 animate-fade-up animation-delay-200">
            Elegance
            <span className="block text-gradient-gold font-medium">Redefined</span>
          </h1>

          {/* Description */}
          <p className="font-body text-foreground/70 text-lg md:text-xl leading-relaxed mb-12 max-w-xl opacity-0 animate-fade-up animation-delay-400">
            Discover the art of sophisticated dressing. Ready-made and custom-tailored 
            formal wear crafted for the modern gentleman who demands excellence.
          </p>

          {/* CTA Buttons */}
          <div className="flex flex-col sm:flex-row gap-4 opacity-0 animate-fade-up animation-delay-600">
            <a href="#collection" className="btn-gold text-center">
              View Collection
            </a>
            <a href="#services" className="btn-outline-gold text-center">
              Our Services
            </a>
          </div>
        </div>
      </div>      
    </section>
  );
};

export default HeroSection;
