const AboutSection = () => {
  const stats = [
    { number: "15+", label: "Years of Excellence" },
    { number: "5000+", label: "Suits Crafted" },
    { number: "100%", label: "Customer Satisfaction" },
  ];

  return (
    <section id="about" className="py-24 md:py-32 bg-background relative overflow-hidden">
      {/* Background Accent */}
      <div className="absolute top-0 right-0 w-1/3 h-full bg-gradient-to-l from-charcoal to-transparent opacity-50" />

      <div className="container mx-auto px-6 relative">
        <div className="grid lg:grid-cols-2 gap-16 items-center">
          {/* Left Content */}
          <div>
            <p className="font-body text-primary text-sm tracking-[0.3em] uppercase mb-4">
              Our Story
            </p>
            <h2 className="font-display text-4xl md:text-5xl lg:text-6xl text-foreground mb-8 leading-tight">
              Crafting
              <span className="block text-gradient-gold">Excellence</span>
              Since 2009
            </h2>
            
            <div className="space-y-6 font-body text-foreground/70 leading-relaxed">
              <p>
                At Wasfah, we believe that true elegance lies in the details. For over 
                a decade, we have been the trusted choice for discerning gentlemen who 
                understand that a perfectly tailored suit is more than clothingit's an 
                expression of success.
              </p>
              <p>
                Our master tailors blend traditional craftsmanship with contemporary 
                design, creating formal wear that commands attention and exudes confidence. 
                Every stitch tells a story of dedication to perfection.
              </p>
            </div>

            {/* Values */}
            <div className="mt-10 grid grid-cols-3 gap-6">
              <div className="text-center">
                <div className="w-12 h-12 mx-auto mb-3 border border-primary/0 flex items-center justify-center">
                  <svg className="w-6 h-6 text-primary" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                </div>
                <p className="font-body text-xs tracking-wider uppercase text-foreground/60">Quality</p>
              </div>
              <div className="text-center">
                <div className="w-12 h-12 mx-auto mb-3 border border-primary/0 flex items-center justify-center">
                  <svg className="w-6 h-6 text-primary" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                </div>
                <p className="font-body text-xs tracking-wider uppercase text-foreground/60">Precision</p>
              </div>
              <div className="text-center">
                <div className="w-12 h-12 mx-auto mb-3 border border-primary/0 flex items-center justify-center">
                  <svg className="w-6 h-6 text-primary" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M5 3v4M3 5h4M6 17v4m-2-2h4m5-16l2.286 6.857L21 12l-5.714 2.143L13 21l-2.286-6.857L5 12l5.714-2.143L13 3z" />
                  </svg>
                </div>
                <p className="font-body text-xs tracking-wider uppercase text-foreground/60">Elegance</p>
              </div>
            </div>
          </div>

          {/* Right Stats */}
          <div className="relative">
            <div className="grid gap-8">
              {stats.map((stat, index) => (
                <div
                  key={stat.label}
                  className="flex items-center gap-8 p-8 bg-card border border-border/50 hover:border-primary/30 transition-colors duration-300"
                >
                  <span className="font-display text-5xl md:text-6xl text-gradient-gold">
                    {stat.number}
                  </span>
                  <span className="font-body text-sm tracking-widest uppercase text-foreground/60">
                    {stat.label}
                  </span>
                </div>
              ))}
            </div>

            {/* Decorative Element */}
            <div className="absolute -bottom-12 -right-12 w-48 h-48 border border-primary/20" />
          </div>
        </div>
      </div>
    </section>
  );
};

export default AboutSection;
