import collection1 from "@/assets/collection-1.jpg";
import collection2 from "@/assets/collection-2.jpg";
import collection3 from "@/assets/collection-3.jpg";

const CollectionSection = () => {
  const collections = [
    {
      image: collection1,
      title: "Executive Suits",
      category: "Business Formal",
    },
    {
      image: collection2,
      title: "Power Collection",
      category: "Classic Tailoring",
    },
    {
      image: collection3,
      title: "Evening Elegance",
      category: "Black Classics",
    },
  ];

  return (
    <section id="collection" className="py-24 md:py-32 bg-black">
      <div className="container mx-auto px-6">
        {/* Section Header */}
        <div className="flex flex-col md:flex-row md:items-end md:justify-between mb-16">
          <div>
            <p className="font-body text-primary text-sm tracking-[0.3em] uppercase mb-4">
              Curated Excellence
            </p>
            <h2 className="font-display text-4xl md:text-5xl lg:text-6xl text-foreground">
              The <span className="text-gradient-gold">Collection</span>
            </h2>
          </div>
          <a
            href="#contact"
            className="hidden md:inline-flex items-center gap-2 font-body text-sm tracking-widest uppercase text-primary hover:text-gold-light transition-colors duration-300 mt-6 md:mt-0"
          >
            View All
            <svg
              className="w-4 h-4"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M17 8l4 4m0 0l-4 4m4-4H3"
              />
            </svg>
          </a>
        </div>

        {/* Collection Grid */}
        <div className="grid md:grid-cols-3 gap-6 lg:gap-8">
          {collections.map((item, index) => (
            <div
              key={item.title}
              className="group relative overflow-hidden cursor-pointer"
              style={{ animationDelay: `${index * 200}ms` }}
            >
              {/* Image Container */}
              <div className="relative aspect-[3/4] overflow-hidden">
                <img
                  src={item.image}
                  alt={item.title}
                  className="w-full h-full object-cover transition-all duration-700 group-hover:scale-110"
                />
                
                {/* Overlay */}
                <div className="absolute inset-0 bg-gradient-to-t from-background via-background/20 to-transparent opacity-60 group-hover:opacity-80 transition-opacity duration-500" />
                
                {/* Border Effect */}
                <div className="absolute inset-4 border border-primary/0 group-hover:border-primary/50 transition-all duration-500" />
              </div>

              {/* Content */}
              <div className="absolute bottom-0 left-0 right-0 p-6 md:p-8">
                <p className="font-body text-primary text-xs tracking-[0.2em] uppercase mb-2 opacity-0 translate-y-4 group-hover:opacity-100 group-hover:translate-y-0 transition-all duration-500">
                  {item.category}
                </p>
                <h3 className="font-display text-2xl md:text-3xl text-foreground mb-4">
                  {item.title}
                </h3>
                <div className="flex items-center gap-2 opacity-0 translate-y-4 group-hover:opacity-100 group-hover:translate-y-0 transition-all duration-500 delay-100">
                  <span className="font-body text-sm tracking-widest uppercase text-primary">
                    Explore
                  </span>
                  <svg
                    className="w-4 h-4 text-primary"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M17 8l4 4m0 0l-4 4m4-4H3"
                    />
                  </svg>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Mobile View All */}
        <div className="md:hidden text-center mt-10">
          <a href="#contact" className="btn-outline-gold">
            View Full Collection
          </a>
        </div>
      </div>
    </section>
  );
};

export default CollectionSection;
