import { useState } from "react";

const ContactSection = () => {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phone: "",
    service: "",
    message: "",
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Handle form submission
    console.log("Form submitted:", formData);
  };

  return (
    <section id="contact" className="py-24 md:py-32 bg-charcoal relative">
      {/* Background Pattern */}
      <div className="absolute inset-0 opacity-5">
        <div className="absolute inset-0" style={{
          backgroundImage: `repeating-linear-gradient(
            45deg,
            hsl(43 90% 50%),
            hsl(43 90% 50%) 1px,
            transparent 1px,
            transparent 20px
          )`,
        }} />
      </div>

      <div className="container mx-auto px-6 relative">
        <div className="grid lg:grid-cols-2 gap-16">
          {/* Left Content */}
          <div>
            <p className="font-body text-primary text-sm tracking-[0.3em] uppercase mb-4">
              Get in Touch
            </p>
            <h2 className="font-display text-4xl md:text-5xl lg:text-6xl text-foreground mb-8">
              Book Your <br />
              <span className="text-gradient-gold">Appointment</span>
            </h2>
            <p className="font-body text-foreground/60 leading-relaxed mb-12 max-w-md">
              Experience personalized service from our expert consultants. 
              Schedule a private appointment at our boutique and begin your 
              journey to impeccable style.
            </p>

            {/* Contact Info */}
            <div className="space-y-6">
              <div className="flex items-center gap-4">
                <div className="w-12 h-12 bg-primary/10 flex items-center justify-center">
                  <svg className="w-5 h-5 text-primary" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                  </svg>
                </div>
                <div>
                  <p className="font-body text-xs tracking-wider uppercase text-foreground/50 mb-1">Visit Us</p>
                  <p className="font-body text-foreground">Adress Line</p>
                </div>
              </div>

              <div className="flex items-center gap-4">
                <div className="w-12 h-12 bg-primary/10 flex items-center justify-center">
                  <svg className="w-5 h-5 text-primary" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z" />
                  </svg>
                </div>
                <div>
                  <p className="font-body text-xs tracking-wider uppercase text-foreground/50 mb-1">Call Us</p>
                  <p className="font-body text-foreground">+971 4 XXX XXXX</p>
                </div>
              </div>

              <div className="flex items-center gap-4">
                <div className="w-12 h-12 bg-primary/10 flex items-center justify-center">
                  <svg className="w-5 h-5 text-primary" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                  </svg>
                </div>
                <div>
                  <p className="font-body text-xs tracking-wider uppercase text-foreground/50 mb-1">Email</p>
                  <p className="font-body text-foreground">appointments@wasfah.ae</p>
                </div>
              </div>
            </div>
          </div>

          {/* Right Form */}
          <div className="bg-card border border-border/50 p-8 md:p-12">
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="grid md:grid-cols-2 gap-6">
                <div>
                  <label className="block font-body text-xs tracking-wider uppercase text-foreground/50 mb-2">
                    Your Name
                  </label>
                  <input
                    type="text"
                    value={formData.name}
                    onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                    className="w-full bg-background border border-border/50 px-4 py-3 font-body text-foreground focus:border-primary focus:outline-none transition-colors"
                    placeholder="Your name"
                    required
                  />
                </div>
                <div>
                  <label className="block font-body text-xs tracking-wider uppercase text-foreground/50 mb-2">
                    Email
                  </label>
                  <input
                    type="email"
                    value={formData.email}
                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                    className="w-full bg-background border border-border/50 px-4 py-3 font-body text-foreground focus:border-primary focus:outline-none transition-colors"
                    placeholder="name@example.com"
                    required
                  />
                </div>
              </div>

              <div className="grid md:grid-cols-2 gap-6">
                <div>
                  <label className="block font-body text-xs tracking-wider uppercase text-foreground/50 mb-2">
                    Phone
                  </label>
                  <input
                    type="tel"
                    value={formData.phone}
                    onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                    className="w-full bg-background border border-border/50 px-4 py-3 font-body text-foreground focus:border-primary focus:outline-none transition-colors"
                    placeholder="+971 XX XXX XXXX"
                  />
                </div>
                <div>
                  <label className="block font-body text-xs tracking-wider uppercase text-foreground/50 mb-2">
                    Service
                  </label>
                  <select
                    value={formData.service}
                    onChange={(e) => setFormData({ ...formData, service: e.target.value })}
                    className="w-full bg-background border border-border/50 px-4 py-3 font-body text-foreground focus:border-primary focus:outline-none transition-colors"
                    required
                  >
                    <option value="">Select Service</option>
                    <option value="ready-made">Ready-Made Collection</option>
                    <option value="custom-made">Custom-Made Tailoring</option>
                    <option value="consultation">Style Consultation</option>
                  </select>
                </div>
              </div>

              <div>
                <label className="block font-body text-xs tracking-wider uppercase text-foreground/50 mb-2">
                  Message
                </label>
                <textarea
                  value={formData.message}
                  onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                  rows={4}
                  className="w-full bg-background border border-border/50 px-4 py-3 font-body text-foreground focus:border-primary focus:outline-none transition-colors resize-none"
                  placeholder="Tell us about your requirements..."
                />
              </div>

              <button type="submit" className="w-full btn-gold">
                Request Appointment
              </button>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ContactSection;
