import readyMade from "@/assets/ready-made.jpg";
import customTailoring from "@/assets/custom-tailoring.jpg";
import { ShoppingBag, Ruler, ArrowRight } from "lucide-react";

const ServicesSection = () => {
  const services = [
    {
      title: "Ready-Made Collection",
      subtitle: "Shop Now",
      description:
        "Explore Wasfah's curated selection of premium suits and formal wear, ready for immediate delivery. Each piece reflects our commitment to exceptional craftsmanship and timeless elegance.",
      image: readyMade,
      features: ["Premium Italian Fabrics", "Expert Craftsmanship", "Express Delivery Available"],
      cta: "Browse Collection",
      href: "#shop-ready-made",
      icon: ShoppingBag,
    },
    {
      title: "Custom Tailoring",
      subtitle: "Made for You",
      description:
        "Experience the art of bespoke tailoring at Wasfah. From fabric selection to final fitting, our master tailors create garments that embody your personal style and perfect fit.",
      image: customTailoring,
      features: ["Personal Style Consultation", "Precision Measurements", "Handcrafted to Order"],
      cta: "Start Your Order",
      href: "#custom-order",
      icon: Ruler,
    },
  ];

  return (
    <section id="services" className="py-24 md:py-32 bg-background">
      <div className="container mx-auto px-6">
        {/* Section Header */}
        <div className="text-center mb-20">
          <p className="font-body text-primary text-sm tracking-[0.3em] uppercase mb-4">
            The Wasfah Experience
          </p>
          <h2 className="font-display text-4xl md:text-5xl lg:text-6xl text-foreground mb-6">
            How We <span className="text-gradient-gold">Serve You</span>
          </h2>
          <p className="font-body text-foreground/60 max-w-2xl mx-auto mb-6">
            Whether you seek the convenience of ready-to-wear excellence or the distinction of custom tailoring, 
            Wasfah delivers uncompromising quality for the modern gentleman.
          </p>
          <div className="separator-gold w-24 mx-auto" />
        </div>

        {/* Services Grid */}
        <div className="grid md:grid-cols-2 gap-8 lg:gap-12">
          {services.map((service, index) => (
            <div
              key={service.title}
              className="group relative overflow-hidden bg-card border border-border/50 hover:border-primary/50 transition-all duration-500"
            >
              {/* Image */}
              <div className="relative h-80 overflow-hidden">
                <img
                  src={service.image}
                  alt={service.title}
                  className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110"
                />
                <div className="absolute inset-0 bg-gradient-to-t from-card via-card/50 to-transparent" />
                
                {/* Icon Badge */}
                <div className="absolute top-6 left-6 w-12 h-12 bg-primary/90 flex items-center justify-center">
                  <service.icon className="w-6 h-6 text-primary-foreground" />
                </div>
              </div>

              {/* Content */}
              <div className="p-8 md:p-10">
                <p className="font-body text-primary text-xs tracking-[0.2em] uppercase mb-2">
                  {service.subtitle}
                </p>
                <h3 className="font-display text-3xl md:text-4xl text-foreground mb-4">
                  {service.title}
                </h3>
                <p className="font-body text-foreground/60 leading-relaxed mb-6">
                  {service.description}
                </p>

                {/* Features */}
                <ul className="space-y-3 mb-8">
                  {service.features.map((feature) => (
                    <li
                      key={feature}
                      className="flex items-center gap-3 font-body text-sm text-foreground/70"
                    >
                      <span className="w-1.5 h-1.5 bg-primary" />
                      {feature}
                    </li>
                  ))}
                </ul>

                {/* CTA Button */}
                <a
                  href={service.href}
                  className="inline-flex items-center gap-3 bg-primary text-primary-foreground font-body text-sm tracking-widest uppercase px-6 py-3 hover:shadow-gold transition-all duration-300 group/btn"
                >
                  {service.cta}
                  <ArrowRight className="w-4 h-4 transition-transform duration-300 group-hover/btn:translate-x-1" />
                </a>
              </div>

              {/* Index Number */}
              <div className="absolute top-6 right-6 font-display text-6xl font-light text-primary/10">
                0{index + 1}
              </div>
            </div>
          ))}
        </div>

        {/* Bottom CTA */}
        <div className="text-center mt-16 pt-16 border-t border-border/30">
          <p className="font-body text-foreground/50 text-sm mb-4">
            Not sure which option suits you best?
          </p>
          <a
            href="#contact"
            className="inline-flex items-center gap-2 font-body text-sm tracking-widest uppercase text-primary hover:text-gold-light transition-colors duration-300"
          >
            Schedule a Free Consultation
            <ArrowRight className="w-4 h-4" />
          </a>
        </div>
      </div>
    </section>
  );
};

export default ServicesSection;
